import React, {
    useState
} from 'react';
import {
    createFFmpeg,
    fetchFile
} from '@ffmpeg/ffmpeg';
import './App.css';
import {
    Input,
    Button,
    CardMedia,
    Grid,
    LinearProgress,
    Alert
} from '@mui/material';
import DataTable from './DataTable';


function App() {
    const [videoSrc, setVideoSrc] = useState('');
    const [selectedVideo, setSelectedVideo] = useState();
    const [progress, setProgress] = useState(0);
    const [files, setFiles] = useState([]);
    const handleFileInputChange = (e) => {
        setFiles(Array.from(e.target.files).filter(e=> {if (e.name[0]!==".") return e}).map((e) => {
           return {
                id: e.webkitRelativePath,
                fileName: e.name,
                relativePath: e.webkitRelativePath,
                type: e.type,
                metadata: loadMetadata(e)
            }
        }));
        console.log(files);
    };
    const [message, setMessage] = useState('Select file & click start');
    const ffmpeg = createFFmpeg({
        log: false,
        logger: ({
            type,
            message
        }) => {
            if (message !== "use ffmpeg.wasm v0.10.0") console.log(type, message)
        }
    });
    const doTranscode = async() => {
        setMessage('Loading ffmpeg-core.js');
        await ffmpeg.load();
        ffmpeg.setProgress(({
            ratio
        }) => {
            setProgress(ratio*100);
        });
        setMessage('Start transcoding');
        ffmpeg.FS('writeFile', selectedVideo.name, await fetchFile(selectedVideo));
        await ffmpeg.run('-i', selectedVideo.name, '-c:v', 'libx264', 'test.mp4');
        setMessage('Complete transcoding');
        const data = await ffmpeg.FS('readFile', 'test.mp4');
        setVideoSrc(URL.createObjectURL(new Blob([data.buffer], {
            type: 'video/mp4'
        })));
    };
    const loadMetadata = file => new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => {
      let audio = new Audio(reader.result);
      audio.preload="metadata";
      audio.onloadedmetadata = () => {resolve({data: reader.result, duration: audio.duration})};
  }
    reader.onerror = error => reject(error);
});
  return (
<div className="App">
  <Grid
    container
    direction="column"
    alignItems="center"
    spacing={2}
    >
    <Grid item>
      <Alert severity="info">{message}</Alert>
    </Grid>
    <Grid item>
      </Grid>
      <Grid item>
        <Input
        onChange={handleFileInputChange}
        inputProps={{webkitdirectory:'true'}}
        type={"file"}
        />
      </Grid>
      <Grid item sx={{width: '100%'}}>
      <DataTable rows={files}/>
    </Grid>
  </Grid>
</div>
  );
}

export default App;
