const RenderAudio = ({data}) => {
  const {value, loading} = usePromise(data);
  if (loading) return <CircularProgress />;
  // return <audio controls src={value}></audio>;
  return <div style={{
    width: value.duration*50+"px",
    height: "20px",
    backgroundColor: "#b8ddff",
    textAlign: "left",
  }}>{value.duration.toFixed(2)}</div>
}

const PreviewAudioDuration = ({data}) => {
  const {value, loading} = usePromise(data);
  if (loading) return <CircularProgress />;
  return <audio controls src={value.data}></audio>;
  // return value.data
}

const AudioDuration = ({data}) => {
  const {value, loading} = usePromise(data);
  if (loading) return 0;
  return `${value.duration.toFixed(2)}`;
  // return value.data
}

