const columns = [
	{
    field: 'id',
    headerName: 'ID',
    flex: 1,
    // description: 'This column has a value getter and is not sortable.',
    // sortable: false,
  },
  { 
  	field: 'fileName', 
  	headerName: 'FileName',
  	sortable: true,
  	flex: 1,
  	// width: 90
  },
  {
    field: 'relativePath',
    headerName: 'Path',
    sortable: true,
    flex: 1,
  },
  {
    field: 'type',
    headerName: 'Type',
    flex: 1,
  },
  {
    field: 'preview',
    headerName: 'Preview',
    flex: 1,
    renderCell: (params) => (
    	<PreviewAudioDuration data={params.row.metadata}/>
    	)
  },
  {
    field: 'duration',
    headerName: 'Duration',
    // type: 'number',
    flex: 1,
    renderCell: (params) => (
    	<RenderAudio data={params.row.metadata}/>
    	),
    valueGetter: async(params) =>
      {
      	const {duration} = await params.row.metadata;
      	return duration
      },
    // description: 'This column has a value getter and is not sortable.',
    // sortable: false,
    // valueGetter: (params) =>
    //   `${params.row.firstName || ''} ${params.row.lastName || ''}`,
  },
];


export default function DataTable({rows}) {
	const getRowSpacing = React.useCallback((params) => {
    return {
      top: 0,
      bottom: 10,
    };
  }, []);

  return (
  	<Box sx={{ width: '100%' }}>
  	<DataGrid
  		autoHeight
        rows={rows}
        columns={columns}
        pageSize={100}
        rowsPerPageOptions={[5]}
        checkboxSelection
        disableSelectionOnClick
		getRowSpacing={getRowSpacing}
        // experimentalFeatures={{ newEditingApi: true }}
      />
      </Box>
  	)
}
